.background{
    width: 100%;
    height: 100vh;
    display: block;
    z-index: -999;
    position: fixed;
    top: 0px;
    background-image: linear-gradient(135deg,#667eea 0,#764ba2 100%);
}
.background .inner-container {
  width: 90%;
  margin: 0 auto;
}
.background .title {
  position: relative;
  display: block;
  top: 30vh;
}
.background .title h1 {
  font: normal 3.6rem "Shrikhand", Helvetica, sans-serif;
  color: #fff;
  letter-spacing: 0.05em;
  text-align: center;
  text-shadow: 0 0 20px rgba(192, 202, 246, 0.9), -5px -5px 0 rgba(194, 183, 254, 0.5), 5px 5px 0 rgba(149, 169, 255, 0.6);
}
.background .button {
  font: 400 1rem "Rubik", Helvetica, sans-serif;
  color: #667eea;
  background-color: rgba(255, 255, 255, 0.75);
  text-align: center;
  margin: 0 auto;
  padding: 1.5em 1em;
  border-radius: 4px;
  text-decoration: none;
  cursor: pointer;
  display: block;
  position: relative;
  width: 200px;
  z-index: 100;
  text-transform: uppercase;
  -webkit-transition: ease-in 0.3s;
  transition: ease-in 0.3s;
}
.background .button:hover {
  background-color: #fff;
  -webkit-box-shadow: 0px 10px 14px -3px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 10px 14px -3px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
